import React, { useState } from 'react';
import Task from './Task';
import TextInput from './TextInput';
import '../App.css';

function App (){
  const [editMode, setEditMode] = useState(false);
  const [items, setItems] = useState([
    // {
    //   id: 0,
    //   text: 'Строка 1',
    //   active: true
    // },
    // {
    //   id: 1,
    //   text: 'Строка 2',
    //   active: true
    // }
  ]);

  let doneTask = (id) => {
    const index = items.map(task => task.id).indexOf(id);
    const tasks = items.slice();
    tasks[index].active = false;
    setItems(tasks);
  }

  let removeTask = (id) => {
    setItems(items.filter((item) => item.id !== id));
    for(let i in items)
      items[i].id = i;
  }

  let editTask = (id, task) => {
    const index = items.map(task => task.id).indexOf(id);
    const tasks = items.slice();
    tasks[index].text = task;
    setItems(tasks);
  }

  let addTask = (task) => {
    setItems([...items,
      {
        id: items.length,
        text: task,
        active: true
      }]);
  }

  return (
    <div className="App">
      <div className="title-bar">
        ToDo List
        <a href="#edit-list" onClick={() => setEditMode(editMode? false : true)}><img className="task-list-button" src="./list-24px.svg" /></a>
      </div>
      {items.map(task => (
        <Task item={task} key={task.id} doneTask={() => doneTask(task.id)} editTask={editTask} removeTask={removeTask} editBar={editMode}></Task>
      ))}
      <TextInput addTask={addTask}></TextInput>
    </div>
  );
}

export default App;
