import React, { useState } from 'react';
import TextInput from './TextInput';

function Task({ item, ...props }) {
  const [editMode, setEditMode] = useState(false);

  const TaskCheckBox = () => {
    const style = {
      visibility: (props.editBar)? 'hidden': ''
    };
    if (item.active) {
      return (
        <input type="checkbox" style={style} onClick={props.doneTask} />
      );
    }
    return (
      <input type="checkbox" style={style} checked disabled />
    );
  };

  let editTask = (task) => {
    props.editTask(item.id, task);
    setEditMode(false);
  }


  if (editMode)
  return (<TextInput editTask={editTask} value={item.text}></TextInput>);

  const className = 'item' + (!item.active ? '-done' : '');
  return (
    <div className={className}>
      <TaskCheckBox></TaskCheckBox>
      <div className="item-text">{item.text}</div>
      <div className="edit-bar">
      {(props.editBar && item.active)? (<a href="#edit" onClick={() => setEditMode(true)}><img className="task-edit-button" src="./edit-24px.svg" /></a>) : ''}
      {(props.editBar)? (<a href="#remove" onClick={() => props.removeTask(item.id)}><img className="task-clear-button" src="./clear-24px.svg" /></a>) : ''}
      </div>
    </div>
  );
}

export default Task;